import Card from '../scripts/components/Card.js';
import FormValidator from '../scripts/components/FormValidator.js';
import Section from '../scripts/components/Section.js';
import UserInfo from '../scripts/components/UserInfo.js';
import PopupWithForm from '../scripts/components/PopupWithForm.js';
import PopupWithImage from '../scripts/components/PopupWithImage.js';
import Api from '../scripts/components/Api.js';
import {
  validationConfig,
  initialCards,
  popupProfile,
  popupEditAvatar,
  popupAddPhoto,
  popupConfirm,
  buttonEditProfile,
  buttonEditAvatar,
  nameProfile,
  jobProfile,
  avatarProfile,
  buttonAddPhoto,
  cardTemplate,
  popupPhoto,
  formProfile,
  nameInput,
  jobInput,
  formPhoto,
} from '../scripts/utils/constants.js';
import './index.css';
import Popup from '../scripts/components/Popup.js';

const API_CONFIG = {
  baseUrl: 'https://mesto.nomoreparties.co/v1/cohort-44',
  headers: {
    authorization: '78b845d7-f9bb-43fd-9d7f-fb92a3c4ec96',
    'Content-Type': 'application/json',
  },
};

const api = new Api(API_CONFIG);

api.getUser().then((res) => console.log(res));

api.getInitialCards().then((items) => {
  // * создать карточку
  const createCard = (item) => {
    const card = new Card(
      {
        data: item,
        handleCardClick: () => {
          openPhotoPopup.open(item);
        },
      },
      cardTemplate
    );
    const cardElement = card.generateCard();

    return cardElement;
  };

  // * рендер карт
  const renderCard = (item) => {
    const cardElement = createCard(item);
    defaultCards.addItem(cardElement);
  };

  // * отрисовка карт
  const defaultCards = new Section(
    {
      data: items,
      renderer: renderCard,
    },
    '.cards'
  );

  defaultCards.renderItems();
});

// * валидация
const validateFormProfile = new FormValidator(validationConfig, formProfile);
const validateFormCard = new FormValidator(validationConfig, formPhoto);

